      'animation': true,
      'tooltip': { 'displayMode': 'union' },
      'interactivity': { 'hoverMode': 'by-x' },
      'series': [{
        'seriesType': 'column',
        'name': 'Сумма по полю Масса разгруженная',
        'data': [
          { 'x': '3', 'value': 13278.70 },
          { 'x': '4', 'value': 4592.25 },
