        ]
      }],
      'type': 'column'
    }
  };

  export default {
    data() {
      return {
        chart: null,
        options
      }
    },

    mounted() {
      if (!this.chart && this.options) {
        this.init();
      }
    },

    methods: {
      init() {
        if (!this.chart && this.options) {
          let _Anychart = this.Anychart || Anychart;
          this.chart = new _Anychart.fromJson(this.options);
          this.chart.container(this.$refs.chart).draw();
        }
      }
    },

    beforeDestroy() {
      if (this.chart) {
        this.chart.dispose();
        this.chart = null;
      }
    }
  }
</script>

<style scoped>
  .chart {
    width: 800px;
    height: 600px;
  }
</style>